import { Controller } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import selectTableSelection from './selectTableSelection';

export default function selectWholeTable(controller: Controller, table: Table) {
  const tableSelection = {
    startRowIndex: 0,
    startColIndex: 0,
    endRowIndex: table.nodes.length - 1,
    endColIndex: (table.data.colsWidth || []).length - 1,
    key: table.key,
  };
  return selectTableSelection(controller, table, tableSelection);
}
